import { useEffect, useState } from 'react';
import { Container, Row, Col, Card, CardImg, CardBody, CardTitle, CardSubtitle, CardText,Button } from 'reactstrap'
function App() {
  const [productos, setProductos] = useState([])
  useEffect(() => {
    fetch('http://localhost:8080/api/productos/listar')
    .then(response => response.json())
    .then(data => setProductos(data))
  },[])
  return (
   <Container>
     <Row>
       <h1>Productos</h1>
       {productos.map( producto =>
          <Col md="3" key={producto.id}>
            <Card>
              <CardImg top width="10%" src={producto.thumbnail} alt="Card image cap" />
              <CardBody>
                <CardTitle tag="h5">{producto.title}</CardTitle>
                <CardSubtitle tag="h6" className="mb-2 text-muted">Card subtitle</CardSubtitle>
                <CardText>Some quick example text to build on the card title and make up the bulk of the card's content.</CardText>
                <Button>Button</Button>
              </CardBody>
            </Card>
          </Col>
       )}
     </Row>
   </Container>
  );
}

export default App;
